<template>
   <div class ="container">
     <div class="add-item">
      <div class="add-icon" @click="createPost">
        <i class="fas fa-plus"></i>
      </div>
      <form @submit.prevent="createPost">
        <input type="text" placeholder="Add a to-do..." v-model="text">      </form>
      
    </div>

     <hr>
     <p class="error" v-if="error">{{ error }}</p>
     <div class ="post-container">
       <div class="post"
       v-for="(post, index) in posts"
       v-bind:item="post"
       v-bind:index="index"
       v-bind:key="post._id"
       v-on:dblclick="deletePost(post._id)"
       >
       {{ `${post.createdAt.getDate()}/${post.createdAt.getMonth()}/${post.createdAt.getFullYear()}` }}
       <p class="text">{{ post.text }}</p>
     </div>
   </div>
   </div>
</template>

<script>
import PostService from '../PostService';

export default {
  name: 'PostComponent',
  data(){
    return {
      posts: [],
      error: '',
      text: ''
    }
  },
  async created(){
    try{
      this.posts = await PostService.getPosts();
    }catch(err){
      this.error = err.message;
    }
  },
  methods: {
    async createPost(){
      await PostService.insertPost(this.text);
      this.posts = await PostService.getPosts();
    },
    async deletePost(id){
    await PostService.deletePost(id);
    this.posts = await PostService.getPosts();
    }
  }
};
</script>


<style lang="scss" scoped>
  .container {
    padding: 10px;
    width: calc(100% - 20px);
    min-height: calc(100% - 20px);

    @mixin feature-icon($color) {
      grid-column-start: 3;
      grid-column-end: 3;
      display: flex;
      justify-content: center;
      align-items: center;
      color: $color;
      font-size: 1.6rem;
      margin: 0 15px;
      cursor: pointer;
    }

    .add-item {
      display: grid;
      grid-template-columns: 70px auto 70px;
      grid-template-rows: 60px;
      width: 100%;
      height: 60px;
      background: rgba($color: #000000, $alpha: .3);
      border-radius: 5px;
      overflow: hidden;

      .add-icon {
        grid-column-start: 1;
        grid-column-end: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #fff;
        font-size: 2rem;
        cursor: pointer;
      }

      input {
        grid-column-start: 2;
        grid-column-end: 2;
        background: none;
        border: none;
        width: 100%;
        height: 60px;
        color: #fff;
        font-size: 1.6rem;

        &::placeholder {
          color: #fff;
        }

        &:focus {
          outline: none;
        }
      }

      .feature-icon {
        @include feature-icon(#fff);
      }
    }

    .post {
    position: relative;
    border: 1px solid #5bd658;
    background-color: rgba($color: #bcffb8, $alpha: .3);
    border-radius: 5px;
    padding: 10px 10px 30px 10px;
    margin-bottom: 2px;
    height: 50px;
    font-size: 1.3rem;
}
  }
  
</style>